.boton {
    background-color: #dadada;
    border: none;
    padding: 8px 20px;
    cursor: pointer;
    border-radius: 8px;
    color: black;
    height: 100%;
    width: 100%;

    &:hover {
        background-color: #111d64;
        color: white;
    }

    &:disabled {
        background-color: #c7c7c7;
        color: black;
        opacity: 0.7;
        cursor: not-allowed;
    }

    &.formulario.selected {
        background-color: #1229ad;
        color: white;
    }

    &.agregar {
        background-color: #1934ac;
        color: white;

        &:hover {
            background-color: #14246e;
        }
    }

    &.guardar {
        background-color: #19ac34;
        color: white;

        &:hover {
            background-color: #106e22;
        }
    }

    &.eliminar {
        background-color: #df2626;
        color: white;

        &:hover {
            background-color: #6e1010;
        }
    }

    &.imagenes {
        background-color: #a4b823;
        color: white;

        &:hover {
            background-color: #646f1e;
        }
    }
}